
import rest from '../scripts/restClient';
import Scroller from 'vux/src/components/scroller/index.vue'

import {
getShopDto,
getShop,
doAuthorize
} from '../scripts/global';

export default{
name:'storeItem',
data () {
return {
searchParam: {    //
module: "interact",
relationType:"store_item",
relationUuid:"85cf4755977d488a81820be221cbe333"
},
query: this.$route.query,
activeCenterUrl:'',
shopDto:getShopDto(),    //
actRuler: {    //
title: '',
detail: [

]
},
actDetail: '0xxx',    //
prizeInfo: {    //
detail: 'xxx',
useMethod: function(){},
methodText: '',
prizeRecordId: null
},
goodIndex: null,    //
toastPrizeGotIsShow: false,    //
toastCouponGotIsShow: false,    //
rulerDialogIsShow: false,    //
addDialogIsShow: false,   //Dialog
prizeDialogIsShow: false,   //Dialog


prizeGot: false,    //
minStoreNum: 0,    //
storeNum: 0,    //
actStatus: false,    //
goodsList: [    //

]
}
},
computed: {
prizeActive: function () {    //
if (this.storeNum >= this.minStoreNum) {
return true;
}
return false;
},
getPrizeBtnText: function () {    //
return this.prizeGot ? '' : '';
},
},
methods: {
init: function(){

var self=this;
self.$vux.loading.show('');
this.getList();    //











getShop(self.query).then(function(shopDto) {
self.shopDto = shopDto;
self.activeCenterUrl = shopDto.interactUrl;
self.$vux.loading.hide();

var params=$.extend({},self.query,shopDto);
doAuthorize(params).then(function(){
self.check().then(self.activateAct);
});
});
},
getUrlParam: function() {    //URL
var self = this
return new Promise(function(resolve,reject){
self.searchParam.module = self.query.module;
self.searchParam.relationType = self.query.relationType;
self.searchParam.relationUuid = self.query.relationUuid;
resolve();
})










},
getList: function(){    //
var self=this;
var searchParam = self.searchParam;

return new Promise (function(resolve,reject){
var action = {
successCallback: function(data) {
console.log(data)

var _goodsList = self.goodsList.concat([])
self.goodsList = _goodsList.concat(data.data.storeItemModel.itemModels);
self.minStoreNum = data.data.storeItemModel.storeItem.minStoreNum;
self.getActDetailAndPrizeInfo(data);
self.getActRule(data);
resolve ();
},
failCallback: function (data) {
console.log(data);
}
};
rest.get("common/activity", searchParam, action);
})

},
getAddNum: function (data) {    //
var _storeNum = 0;
this.goodsList = data.data.itemModels;
this.goodsList.forEach(function(good){
if (good.isAdd) {
_storeNum += 1;
}
})
this.storeNum = _storeNum;
},
getActDetailAndPrizeInfo: function(data){    //
var self = this,
_minStoreNum = this.minStoreNum,
_actDetail = '',
_prizeInfo = {
detail: 'xxx',
useMethod: function(){
window.location.href='https://h5.m.taobao.com/app/cpkg/portal.html';
},
methodText: '',
prizeRecordId: null
},
prizeData = data.data.storeItemModel.storeItem.prizes[0];

switch(prizeData.type)
{
case 'coupon':
_prizeInfo.useMethod = function () {
var action = {
successCallback: function (data) {
window.location.href= data.data;
self.$vux.toast.show({
text: "",
position: "center"
})
},
failCallback: function (data) {
console.log(data);
self.toastCouponGotIsShow = true;
setTimeout(function(){
self.toastCouponGotIsShow = false;
},1000)
}
};
rest.get("dragon/prizeRecord", {prizeRecordId: self.prizeInfo.prizeRecordId}, action);
};
_prizeInfo.methodText = '';
if(prizeData.subType == 'coupon_shop'){
_actDetail = '' + _minStoreNum + '' + prizeData.denomination/100 + '';
_prizeInfo.detail = '' + prizeData.denomination/100 + ''
}else if(prizeData.subType == 'coupon_goods'){
_actDetail = '' + _minStoreNum + '' + prizeData.denomination/100 + '';
_prizeInfo.detail = '' + prizeData.denomination/100 + '';
}
break;
case 'alipay_red':
_actDetail = '' + _minStoreNum + '' + prizeData.denomination/100 + '';
_prizeInfo.detail = '' + prizeData.denomination/100 + '';
break;
case 'flow':
_actDetail = '' + _minStoreNum + '' + prizeData.denomination/100 + 'M';
_prizeInfo.detail = '' + prizeData.denomination/100 + 'M';
break;
case 'point':
_actDetail = '' + _minStoreNum + '' + prizeData.denomination/100 + (self.shopDto.unit||'') + '';
_prizeInfo.detail = '' + prizeData.denomination/100 + (self.shopDto.unit||'') + '';
_prizeInfo.methodText = '';
_prizeInfo.useMethod = function () {
window.location.href=self.shopDto.mallUrl;
};
break;
case 'ticket':
_actDetail = '' + _minStoreNum + '' + prizeData.denomination/100 + '';
_prizeInfo.detail = '' + prizeData.denomination/100 + '';
_prizeInfo.methodText = '';
_prizeInfo.useMethod = function () {
window.location.href=self.shopDto.mallUrl;
};
break;
default:

}

this.actDetail = _actDetail;
this.prizeInfo = _prizeInfo;

},
getActRule: function(data){    //
var actRuleDetail = null,
detailStr = data.data.storeItemModel.storeItem.rule;

actRuleDetail = detailStr.split(/\n/);
this.actRuler.detail = actRuleDetail;

},
check: function(){    //
var self = this;

return new Promise (function(resolve,reject){
var action = {
successCallback: function (data) {
console.log('success',data);
self.prizeGot = data.data.obtained;
self.getAddNum(data);
resolve ();
},
failCallback: function (data) {
console.log('fail',data);
window.location.href='https://qnzg.ews.m.jaeapp.com/#/activityMiss';
}
};
rest.get("dragon/storeItem/check", {relationUuid: self.searchParam.relationUuid}, action);
})

},
activateAct: function(){    //
var self = this;
this.actStatus = true;


},
addToCar: function (idx) {    //
this.goodIndex = idx;
if(!!this.goodsList[this.goodIndex].isAdd){
return;
}
this.addDialogIsShow = true;
},
cancelAdd: function(){    //
this.addDialogIsShow = false;
},
sureAdd: function(idx){    //
this.goodIndex = idx;
if(!!this.goodsList[this.goodIndex].isAdd){
return;
}
var self = this,
obj = {
itemId:self.goodsList[self.goodIndex].id,
relationUuid: self.searchParam.relationUuid
};

if(this.shopDto.env == "local"){//
store();
}else{
Tida.ready({}, function () {
Tida.itemFavor({
itemId: obj.itemId + "",
action: "add"
},function(data){
if(data.ret && data.ret == "HY_SUCCESS"){//android
store();
} else if(data.ret && data.ret[0] == "HY_SUCCESS"){//ios
store();
} else if(data.errorType == 3 && (data.errorCode == "ALREADY_COLLECTION")){//ios
store();
} else if(data.ret && data.ret[0] == "HY_FAILED" && data.WOPC_ERROR && data.WOPC_ERROR.errorCode == "ALREADY_COLLECTION"){
store();
} else if(data.ret && data.ret[0] == "HY_FAILED" && data.WOPC_ERROR && data.WOPC_ERROR.errocCode == "ALREADY_COLLECTION"){
store();
} else if(data.ret && data.ret == "HY_FAILED" && data.WOPC_ERROR && data.WOPC_ERROR.errorCode == "ALREADY_COLLECTION"){
store();
} else if(data.ret && data.ret[0] == "HY_FAILED" && data.type == 1){
store();
} else if(data.ret && data.ret[0] == "HY_FAILED" && data.WOPC_ERROR && data.WOPC_ERROR.errorCode == "WOPC_USER_CANCEL"){//
self.$vux.toast.show({
text: "",
position: "center"
})
} else if(data.errorType == 4 && data.ret[0] == "HY_FAILED"){//ios
self.$vux.toast.show({
text: "",
position: "center"
})
} else {
alert(""+JSON.stringify(data));
}
},function (e) {
alert(":"+JSON.stringify(data));
})
})

};

function store(){
self.addDialogIsShow = false;
var action = {
successCallback: function (data) {
console.log('success');
self.goodsList[self.goodIndex].isAdd = 1;
self.storeNum += 1;
}
};
rest.post("dragon/storeItem/doStoreItem", obj, null, action);
}

},
getPrize: function () {    //
var self = this;
if(!this.prizeActive){
this.toastPrizeGotIsShow = true;
var _this = this;
setTimeout(function(){
_this.toastPrizeGotIsShow = false;
},1000)
return;
}else if(this.prizeGot){
return;
}else{
var action = {
successCallback: function (data) {
console.log('success',data);
self.prizeDialogIsShow = true;
self.prizeGot = true;
self.prizeInfo.prizeRecordId = data.data.prizeRecordId;
}
};
rest.post("dragon/storeItem/doTakeAward", {relationUuid: self.searchParam.relationUuid}, null, action)

}
},
cancelUsePrize: function () {    //
console.log('cancel',this.prizeDialogIsShow)
this.prizeDialogIsShow = false;
},
jumpToShop: function () {    //
window.location.href='https://shop' + this.shopDto.shop.outerShopId + '.taobao.com/';
},
jumpToGood: function (idx) {    //
console.log(idx,this.goodsList)
window.location.href='https://item.taobao.com/item.htm?id=' + this.goodsList[idx].id;
},
scroll: function (e) {    //
if(e.target.scrollTop > 10) {
$('.storeItem>main').css({
top: -3.466666 + 'rem'
})
}
if(e.target.scrollTop == 0) {
$('.storeItem>main').css({
top: 0
})
}
},
stop: function (e) {
console.log('click...');
e.stopPropagation();
}
},
created:function(){
var self = this;
this.getUrlParam().then(self.init);

}
}



